<template>
    <div>
        <v-header>
        </v-header>

        <v-content>
            <div class="attention">
                <div class="notice">
                    <span class="looks">验证码已发送绑定手机{{phone}},请注意查收</span>
                </div>
            </div>
            <div class="section">
                <div class="matter">
                    <div class="title">
                        验证码
                    </div>
                    <div class="edit">
                        <input type="text" v-model="code"  maxlength="6" placeholder="输入验证码" />
                    </div>
                    <span class="color-main text" v-show="seconds == 0" @click="getCode">获取验证码</span>
                    <span class="color-9 text" v-show="seconds > 0">{{seconds}}s后重发</span>
                </div>
            </div>

            <div :class="['confirm-ok']" @click="vaild">验证</div>
        </v-content>
    </div>
</template>
<script>
    import {url, lt} from '../../const'
    import {toast} from '../../assets/js/toast'
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'
    import $ from 'jquery'
    export default{
        route:{
            data({from, to:{query}}){
                this.phone = '';
                this.from = query.from;
                return this.$http.get(url.getPhone).then(
                ({data:{data}}) => {
                this.phone = data.phone;
                })
            }
        },
        ready(){
            this.$on('ready',this.init);
            this.$emit('ready');
            this.isReady = true;
        },
        data(){
            return {
                isReady:false,
                state:false,

                code: '',
                phone: '',
                seconds: 0,
                from: ''
            }
        },
        methods: {
            init(){
                if(lt.has(lt.keys.user)){
                    this.code = '';
                }
            },
            getCode(){
                let vm = this;
                $(".looks").css("visibility","visible");
                if (vm.seconds == 0) {
                    vm.seconds = 60;
                }
                let sec = setInterval(function () {
                    vm.seconds = vm.seconds - 1;
                }, 1000);
                let s = vm.$watch('seconds', function () {
                    if (vm.seconds == 0) {
                        clearInterval(sec);
                        s();
                        $(".looks").css("visibility","hidden");
                    }
                });
            },
            vaild(){
                let vm = this;
                if (!vm.code){
                    toast.alert('请输入验证码');
                    return;
                }
                this.$http.get(url.tradePsdVaild,{code:vm.code}).then(
                        function ({data:{success, data, resultMsg, code}}) {
                            if (success) {
                                window.location.href = "#/wallet/retrieveTradePsd?from=" + this.from;
                            } else {
                                toast.alert(resultMsg);
                            }
                        },function (response){
                            toast.alert('网络异常');
                        })
            }
        },
        watch:{
            'state':function(){
                this.$emit('ready');
            }
        },
        components: {VHeader, VContent}
    }
</script>

<style scoped>
    .matter .right {
        float: right;
        position: absolute;
        right: .3rem;
        color: #999;
        font-size: .15rem;
    }

    .matter span {
        float: right;
        position: absolute;
        right: .0rem;
        height: .45rem;
        background-color: #00CDA9;
        color: #FFFFFF;
        font-size: .15rem;
        width: 1rem;
        border-radius: 0;
        text-align: center;
    }
    .notice {
        height: .2rem;
        line-height: .2rem;
        margin-left: .1rem;
    }
    .attention {
        background-color: #f4f4f4;
    }

    .looks {
        font-size: .1rem;
        color: #aeaeae;
        visibility: hidden;
    }
</style>


